<template>
	<div id="goodstore-index">
		<public-header title="好店"></public-header>
		<div class="content">
			<div class="diy-store-settle van-hairline--bottom">
				<div class="new-info">
					<div class="img-settle"></div>
					<div class="news-swipe">
						<van-swipe :autoplay="3000" :show-indicators="false" vertical>
							<van-swipe-item class="news-swipe-item" v-for="(item, index) in 3" :key="index">
								<div class="font-13 ellipsis w-100">恭喜【<span class="c-danger">茶不思{{item}}</span>】入驻</div>
								<div class="font-13 ellipsis w-100">恭喜【<span class="c-danger">门店名称{{item}}</span>】入驻</div>
							</van-swipe-item>
						</van-swipe>
					</div>
				</div>
				<div class="btn-settle">我要入驻</div>
			</div>
			<div class="diy-goodstore-list">
				<div class="store-item van-hairline--top" v-for="(item, index) in 5" :key="index">
					<div class="img-wrap">
						<img src="https://1.xinzuowl.com/attachment/images/1/2016/07/rqFhB0N0N32qnf59pfUuPbofQNO2Hf.jpg" alt="">
					</div>
					<div class="store-main">
						<div class="flex-lr">
							<div class="flex">
								<div class="store-title">茶不思</div>
								<van-rate v-model="storeRate" :size="12" disabled-color="#ff2d4b" disabled />
								<span class="c-gray font-12 margin-5-l">4.4分</span>
							</div>
							<div class="c-danger">4.2km</div>
						</div>
						<div class="c-gray font-12 margin-10-t">营业时间: 09:00-14:00 17:00-23:00</div>
						<ul class="store-tags">
							<li class="tag-item">提供包间</li>
							<li class="tag-item">免费停车</li>
							<li class="tag-item">免费打包</li>
							<li class="tag-item">提供WIFI</li>
						</ul>
						<div class="discount-box">
							<div class="toggle">
								<div class="icon icon-unfold"></div>
							</div>
							<div class="single-line">
								<img class="discount-icon" src="static/img/discount_b.png">
								<div class="discount-text">满减活动满减活动满减活动满减活动满减活动</div>
							</div>
							<div class="single-line">
								<img class="discount-icon" src="static/img/discount_b.png">
								<div class="discount-text">满减活动</div>
							</div>
						</div>
					</div>
				</div>
				<div class="loaded">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader
		},
		data() {
			return {
				storeRate: 4,
			}
		}
	}
</script>

<style scoped>
	@import "../../../static/css/test.css";
	/*最新入驻*/
	#goodstore-index .diy-store-settle{
		padding: 10px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		background-color: #fff;
	}
	#goodstore-index .diy-store-settle .new-info{
		flex: 1;
		padding-right: 10px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#goodstore-index .diy-store-settle .new-info .img-settle{
		width: 35px;
		height: 35px;
		background: url('../../../static/img/new_settle.png') no-repeat center center;
		background-size: 100% 100%;
	}
	#goodstore-index .diy-store-settle .new-info .news-swipe{
		flex: 1;
		padding-left: 10px;
	}
	#goodstore-index .diy-store-settle .new-info .news-swipe .van-swipe{
		height: 35px;
	}
	#goodstore-index .diy-store-settle .new-info .news-swipe .van-swipe .van-swipe-item{
		width: 100%;
		height: 100%;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#goodstore-index .diy-store-settle .btn-settle{
		width: 80px;
		height: 35px;
		border-radius: 3px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 14px;
		color: #fff;
		background-color: #ff2d4b;
	}
	/*好店商户列表*/
	#goodstore-index .diy-goodstore-list .store-item{
		padding: 12px;
		background-color: #fff;
		font-size: 14px;
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
	}
	#goodstore-index .diy-goodstore-list .store-item:first-child:after{
		display: none;
	}
	#goodstore-index .diy-goodstore-list .store-item .img-wrap{
		width: 70px;
		height: 70px;
		font-size: 0px;
		border-radius: 5px;
		overflow: hidden;
	}
	#goodstore-index .diy-goodstore-list .store-item .img-wrap img{
		width: 100%;
		height: 100%;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main{
		flex: 1;
		padding-left: 15px;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .store-title{
		max-width: 100px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 15px;
		font-weight: 500;
		margin-right: 10px;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .store-tags{
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		margin-right: -10px;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .store-tags .tag-item{
		font-size: 12px;
		color: #ff2d4b;
		background-color: #FFE3E7;
		padding: 3px 5px;
		border-radius: 3px;
		margin: 10px 10px 0 0;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .discount-box{
		width: 100%;
		position: relative;
		padding-top: 4px;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .discount-box .toggle{
		position: absolute;
		right: 0;
		top: 10px;
		font-size: 11px;
		color: #999;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .discount-box .toggle .icon{
		font-size: 11px;
		color: #999;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .discount-box .single-line {
		max-width: 94%;
		padding-top: 6px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		line-height: 12px;
		display:flex;
		flex-flow:row wrap;
		align-items:center;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .discount-box .single-line img{
		width: 15px;
		height: 15px;
	}
	#goodstore-index .diy-goodstore-list .store-item .store-main .discount-box .single-line .discount-text{
		color: #999;
		max-width:90%;
		font-size:12px;
		padding-left:5px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
</style>


